import { graphql, PageProps } from "gatsby";
import { MDXRenderer } from "gatsby-plugin-mdx";
import React, { FC } from "react";
import { Helmet } from "react-helmet";
import Layout from "../components/common/Layout/Layout";
import { GatsbySeoComponent, TopLevelPageJSONLDSchemas } from "../components/common/SEO";
import {
  PolicyContent,
  PolicyContentContainer,
} from "../components/policies/styled";

const PolicyPage = ({ data }: PageProps<Queries.PolicyPageQuery>) => {
  const title = `${data.mdx?.frontmatter?.title} | Telgorithm`

  return (
    <>
      <Helmet title={title}>
      <meta property={title} />
        <meta property="twitter:title" content={title} />
      </Helmet>
      <TopLevelPageJSONLDSchemas />
      <GatsbySeoComponent
        canonicalUrl={"https://www.telgorithm.com/policies/" + data.mdx?.slug}
      />
      <Layout>
        <PolicyContentContainer>
          <PolicyContent>
            <MDXRenderer children={data?.mdx?.body ?? "No content"} />
          </PolicyContent>
        </PolicyContentContainer>
      </Layout>
    </>
  );
};

export default PolicyPage;

export const query = graphql`
  query PolicyPage($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        title
      }
      body
      slug
    }
  }
`;
